.btn {
  display: inline-block;
  border-radius: 5px;
  border: none;
  padding: 10px 15px;
  font-weight: 700;
  cursor: pointer;
  font-size: 1rem;
}

.btn-success {
  background-color: #15ac08;
  transition: background-color 0.5s ease;
  color: #eeeeee;
}
.btn-success:hover {
  background-color: #129407;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none !important;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../img/contabilidad.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
  /* ======  ====== */
  justify-content: center;
  align-items: center;
}

.login {
  box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
  padding: 25px 15px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.6);
  max-width: 300px;
  width: 90%;
}

.input-form {
  margin-bottom: 10px;
  position: relative;
}
.input-form .fa-envelope,
.input-form .fa-key {
  font-size: 1.6rem;
  position: absolute;
  left: 3%;
  top: 15%;
  bottom: 3%;
}
.input-form:last-of-type {
  margin-bottom: 0;
}
.input-form input {
  border-radius: 5px;
  width: 100%;
  border: none;
  padding: 10px 15px 10px 40px;
}
.input-form input:focus {
  outline: none;
}

.w-100 {
  width: 100% !important;
}

.m-1 {
  margin: 10px;
}

.mt-1 {
  margin-top: 10px;
}

.text-uppercase {
  text-transform: uppercase;
}

.fa-sign-in-alt {
  font-size: 1.2rem;
}

.text-right {
  text-align: left;
}

.imagen-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.imagen-logo img {
  max-width: 250px;
  width: 60%;
  margin-top: -40%;
  border: none;
  box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  padding: 0;
}

.opciones {
  margin-top: 15px;
}
.opciones a {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
}
.opciones span {
  font-weight: 700;
  margin-right: 5px;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2 {
  margin: 0;
}

.titulo {
  font-size: 1.4rem;
}

.grid-dashboard {
  position: relative;
  display: flex;
  height: 100vh;
  flex-direction: row;
  color: #fff;
  background-color: #f2f2f2;
}
.grid-dashboard .nav-dashboard {
  background-color: #2c2c2c;
  position: absolute;
  left: -38%;
  transition: left 0.5s ease;
  z-index: 2;
}
.grid-dashboard .nav-dashboard:hover {
  left: 0;
}
@media (min-width: 430px) {
  .grid-dashboard .nav-dashboard {
    left: -45%;
  }
}
@media (min-width: 450px) {
  .grid-dashboard .nav-dashboard {
    left: -42%;
  }
}
@media (min-width: 540px) {
  .grid-dashboard .nav-dashboard {
    position: relative;
    left: 0;
    flex: 1 1 20%;
  }
}
.grid-dashboard .content-dashboard {
  flex: 1 1 80%;
}

.nav-dashboard {
  height: 100%;
}

.logo {
  font-weight: 700;
  font-size: 15px;
  padding: 26px;
  border-bottom: 1px solid #6e6e6e;
  border-right: 1px solid #6e6e6e;
  background-color: #3090df;
}
.logo h2 {
  text-align: center;
  text-transform: uppercase;
}

.info {
  display: flex;
}

.img-info {
  width: 60px;
  border-radius: 50%;
  margin-left: 20px;
}

.header-dashboard {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 20px;
  background-color: #2c2828;
}
@media (min-width: 340px) {
  .header-dashboard {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header-dashboard .head-content {
  order: 2;
  display: flex;
  align-items: center;
  padding: 10px;
  margin-top: 10px;
  justify-content: space-between;
}
.header-dashboard .head-content a {
  color: #fff;
  margin-right: 10px;
}
.header-dashboard .head-content a:last-of-type {
  margin-right: 0;
}
.header-dashboard .head-content a i {
  font-size: 1.8rem;
}
@media (min-width: 340px) {
  .header-dashboard .head-content {
    order: -1;
    margin-top: 0;
  }
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.info-text {
  text-align: right;
}
.info-text p {
  font-weight: 600;
  font-size: 1.2rem;
}

.nav-enlance {
  list-style: none;
  padding: 0;
  margin: 0;
}
.nav-enlance li {
  padding: 15px 25px;
  border-bottom: 2px solid #383838;
}
.nav-enlance li:hover {
  background-color: #242424;
}
.nav-enlance li a {
  color: #fff;
  font-size: 1rem;
}

.titulo-content {
  font-size: 1.9rem;
}

.contenedor {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.flex-beetwen {
  justify-content: space-between;
  margin-bottom: 15px;
}

.panel-content {
  color: #000;
  margin-top: 15px !important;
  overflow-y: scroll;
  background-color: #fff;
  padding: 15px;
  border-radius: 5px;
}

.form-group {
  color: #000;
}

td {
  text-transform: capitalize;
}

.content-dashboard {
  position: relative;
  overflow-y: scroll;
  height: 100%;
}
.content-dashboard .header-dashboard {
  position: sticky;
  z-index: 1;
  top: 0;
}

.nav-enlance li a {
  width: 100%;
  display: block;
}

.title-vc {
  text-transform: capitalize;
  font-weight: 700;
}
.title-vc span {
  font-size: 1.3rem;
}

.direccion-vc {
  text-transform: capitalize;
  font-weight: bold;
}
.direccion-vc span {
  font-weight: 400;
}

.fecha-vc {
  text-transform: capitalize;
  font-weight: 700;
}
.fecha-vc span {
  text-transform: none;
  font-weight: 400;
}

.capital-vc {
  font-size: 1.5rem;
  font-weight: 600;
}
.capital-vc span {
  font-size: 1.3rem;
  font-weight: 400;
}

.spinner {
  margin: 100px auto 0;
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.spinner-body {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1500;
  background-color: rgba(0, 0, 0, 0.171);
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

ul {
  padding-left: 0 !important;
}

#status {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-content: center;
  justify-content: center;
}